import React from 'react';
import {NavLink} from 'react-router-dom';

export default class Home extends React.Component{
  state={
    list:[]
  };
  componentDidMount(){
    console.log(Home.axios)
    // console.log(axios)
    Home.axios({
      url:'/mock/home',
      params:{_page:1,_limit:6}
    }).then(
      res=>this.setState({list:res.data.data})
    )
  }
  render(){
    return (
      <div className="home">
        	<div>
            <header>
              <div className="header">
                <NavLink to="/classify"><i className="icon icon-menu"></i></NavLink>
                <h1>京西商城</h1>
                <i className="icon icon-soso"></i>
              </div>
            </header>
            <div style={{height: "2.5rem"}}></div>
          </div>

          <div className="m-slider" id="slider">
            <div className="ks_dbox ks_ts">
                <div className="ks_wrap">
                  <img src="/assets/img/banner1.jpg" alt="1"/>
                </div>
                <div className="ks_wrap">
                  <img src="/assets/img/banner2.jpg" alt="1"/>
                </div>
                <div className="ks_wrap">
                  <img src="/assets/img/banner3.jpg" alt="1"/>
                </div>
            </div>
            <div className="ks-circles"><ul className="ks_wt"></ul></div>
          </div>

          <nav>
            <div className="nav-box">
              <a href="##"><img src="/assets/img/nav01.png" alt="1"/><span>大聚惠</span></a>
              <a href="##"><img src="/assets/img/nav02.png" alt="1"/><span>海外购</span></a>
              <a href="##"><img src="/assets/img/nav03.png" alt="1"/><span>超市百货</span></a>
              <a href="##"><img src="/assets/img/nav04.png" alt="1"/><span>厂家直销</span></a>
              <a href="##"><img src="/assets/img/nav05.png" alt="1"/><span>美食娱乐</span></a>	
            </div>
          </nav>

          <article>
            <h1>商城客户</h1>
            <div className="client">
              <a href="##"><img src="/assets/img/scyh0.jpg" alt="1"/><span>海绵包包</span></a>
              <a href="##"><img src="/assets/img/scyh3.png" alt="1"/><span>韩国代购</span></a>
              <a href="##"><img src="/assets/img/scyh4.png" alt="1"/><span>ARC潮店</span></a>
              <a href="##"><img src="/assets/img/scyh1.png" alt="1"/><span>欧力女装</span></a>
            </div>
          </article>
          
          <article style={{marginBottom: 0}}>
            <h1>精选促销</h1>
          </article>

          <div className="commodity-box">
            <ul className="commodity">
              {
                this.state.list.map((item)=>{
                  return (
                    <li key={item.id} >
                      <NavLink to={`/details/${item.id}?dataName=home`}>
                          <img src={item.img} alt=""/>
                          <span>{item.title}</span>
                          <span className="price">￥{item.xiangj} <s>￥{item.yuanj}</s></span>
                      </NavLink>
                    </li>
                  )
                })
              }
    
            </ul>	
          </div>
      </div>
    )
  }
}