import React from 'react';

import './css/App.css';
import "../assets/css/bass.css";
import "../assets/css/style.css";
import { Route, Switch, Redirect } from 'react-router-dom';
import Car from "../pages/Car"
import Chant from "../pages/Chant"
import Classify from "../pages/Classify"
import Details from "../pages/Details"
import Home from "../pages/Home"
import ListGoods from "../pages/ListGoods"
import Login from "../pages/Login"
import Order from "../pages/Order"
import Reg from "../pages/Reg"
import Site from "../pages/Site"
import SiteAdd from "../pages/SiteAdd"
import SiteAmend from "../pages/SiteAmend"
import Unity from "../pages/Unity"
import UnityAdd from "../pages/UnityAdd"
import User from "../pages/User"
import ErrorPage from "../pages/ErrorPage";
import Loading from "../components/loading/Loading";

import PubSub from 'pubsub-js'
import Auth from "../guard/Auth";

// import Header from "./Header";

import Footer from "./Footer";
export default class App extends React.Component{
    state={
      bNav:false,
      bFoot:false,
      bLoading:false
    };

    constructor(){
      super();
  
      // 订阅
      this.token = PubSub.subscribe('VIEW_LOADING', (msg, data)=>{
        //msg ~~ VIEW_LOADING
        //data ~~ 发布方传递过来的数据
        console.log('VIEW_LOADING',msg, data);
        this.setState({bLoading:data})
      });
  
    }
  
    componentWillUnmount(){
      PubSub.unsubscribe(this.token) //取消订阅
    }
  
    componentWillReceiveProps(nextProps){
      let path = nextProps.location.pathname;
      // console.log('app',path)
      this.checkPath(path)
    }
    componentDidMount(){
      let path = this.props.location.pathname;
      // console.log('app',path)
      this.checkPath(path)
    }
    checkPath = (path) => {
      if (/home|unity|user/.test(path)){
        this.setState({bFoot:true})
      }
      if (/login|reg|detail|car|unityAdd|order|site|siteAdd|siteAmend|listGoods|details/.test(path)){
        this.setState({bFoot:false})
      }
    };
    render(){
      let {bFoot,bLoading}=this.state;
      return (
        <div className="App">
            {bLoading && <Loading/>}
            <Switch>
                
                <Route path="/home" component={Home}/>
                <Route path="/car" component={Car}/>
                <Route path="/chant" component={Chant}/>
                <Route path="/classify" component={Classify}/>
                <Route path="/listGoods/:id" component={ListGoods}/>
                <Route path="/order" component={Order}/>
                <Route path="/site" component={Site}/>
                <Route path="/siteAdd" component={SiteAdd}/>
                <Route path="/siteAmend/:id" component={SiteAmend}/>
                <Route path="/unity" component={Unity}/>
                <Route path="/unityAdd" component={UnityAdd}/>
                <Auth path="/user" component={User}/>
                <Route path="/login" component={Login}/>
                <Route path="/reg" component={Reg}/>
                <Route path="/details/:id" component={Details}/>

                <Redirect exact from="/" to="/home" />

                <Route component={ErrorPage} />


            </Switch>
            {bFoot?<Footer/>:null}
        </div>
      )
    }
  }
