import React from 'react';
import { Link } from 'react-router-dom'
export default class Login extends React.Component{

  state = {
    user:'',
    content:'',
    list:[]
  };

  changeUser=(ev)=>{
    this.setState({user:ev.target.value})
  };

  changeContent=(ev)=>{
    this.setState({content:ev.target.value})
  };

  render(){
    return (
      <div className="Login">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>登录</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div> 
        
        <div className="Login-text Login-t Login-t-t">
          <label>账号:</label>
          <input type="text" className="Login-user" placeholder="请输入账号" value={this.state.user} onChange={this.changeUser} />
        </div>
        
        <div className="Login-pass Login-t Login-t-t">
          <label>密码:</label>
          <input type="password" className="Login-content" placeholder="请输入密码" value={this.state.content} onChange={this.changeContent} />
        </div>
        
        <div className="Login-radio Login-t">
          <input type="radio" className="Login-la" value="设置为默认登录" id="check-2" name="doc-radio-1"/>
          <label htmlFor="check-2">设置为默认登录</label>
        </div>
        
        <div className="Login-btn Login-t">
            <button>登录</button>
        </div>
        
        <div className="Login-zhuche Login-t"><Link to="/reg">前往注册</Link></div>

      </div>
    )
  }
}