import React from 'react';
import {NavLink} from 'react-router-dom'
export default class Site extends React.Component{
  render(){
    return (
      <div className="Site">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>收货地址</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div> 

         <article className="orderSite">
          <NavLink to="/siteAmend/1">
            <p>收货人：郭小帅&nbsp;&nbsp;13688888888</p>
            <span><em>[默认]</em>广州市番禺区市桥街道81号</span>
            <i className="icon icon-select"></i>
          </NavLink>
          <NavLink to="/siteAmend/2">
            <p>收货人：郭小帅&nbsp;&nbsp;13688888888</p>
            <span>广州市番禺区市桥街道81号</span>
            <i className="icon icon-select"></i>
            </NavLink>
        </article>

        
        <div className="add-site">
          <NavLink className="btn bottom-btn" to="/siteAdd">新增地址</NavLink>	
        </div>
      </div>
    )
  }
}