import React from 'react';

export default class SiteAmend extends React.Component{
  render(){
    return (
      <div className="SiteAmend">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>修改地址</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div>  

        <article className="no-pad">
          <form>
            <div className="input-row input-row2">
                <label>联系人</label>
                <input type="text" placeholder="请填写联系人姓名" defaultValue="郭小帅"/>
              </div>
              <div className="input-row input-row2">
                <label>手机号</label>
                <input type="text" placeholder="请填写手机号码" defaultValue="13688888888"/>
              </div>
              <div className="input-row input-row2">
                <label>所在地区</label>
                <input type="text" defaultValue="广东省广州市番禺区"/>
              </div>
              <div className="input-row input-row2">
                <label>详细地址</label>
                <input type="text" placeholder="街道" defaultValue="市桥街道81号"/>
              </div>
              <div className="setCmp-input select-Industry addSite-radio"/>
              <div className="payment">
                <input type="radio" className="radio-la" defaultValue="设置为默认地址" id="check-2" name="doc-radio-1"/>
                      <label htmlFor="check-2">设置为默认地址</label>
              </div>		
            
          </form>
        </article>

        <div className="add-site">
          <a href="##" className="btn bottom-btn">保存地址</a>	
          <a href="##" className="btn bottom-btn bottom-btn2">删除地址</a>
        </div>

      </div>
    )
  }
}