import React from 'react';

export default class UnityAdd extends React.Component{
  render(){
    return (
      <div className="UnityAdd">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>分享</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div>  

        <article>
            <div className="c-Publish">
              <div className="Pu-text">
                <textarea rows="4" maxLength="50" placeholder="这一刻的想法(50字内)..."></textarea>  
              </div>
              <div className="Pu-img">
                <a href="##" className="Pu-Add-img">
                  <img src="/assets/img/f_addimg.png" alt=""/>
                  <input type="file" accept="image/*"/>
                </a>
              </div>
            </div>
            <div className="submit-btn">
              <button className="btn btn-max btn-forbidden">提交</button>
            </div>
        </article>

      </div>
    )
  }
}