import React from 'react';
import {NavLink} from 'react-router-dom'
import querystring from 'query-string'
export default class Details extends React.Component{
    state={
      data:{},
      banners:[],
      imgs:[],
      zt:{display:"none"}
    };
    componentDidMount(){
      let id = this.props.match.params.id-0;
      let dataName = querystring.parse(this.props.location.search).dataName;
      Details.axios({
        url:`/mock/${dataName}/${id}`
      }).then(
        res=>this.setState({data:res.data.data}),
        res=>this.setState({banners:res.data.data.banners}),
        res=>this.setState({imgs:res.data.data.imgs})
        // res=>console.log(res.data.data.detail.banners)
      )
    }  
  render(){
    let data=this.state.data;
    let banners = this.state.banners;
    let imgs = this.state.imgs;
    return (
      <div className="Details">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>产品详情</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div>

        <div className="product-head">
          {
            banners.map((item)=>{
              return (
                <a href="##" key={item.ids}><img src={item.bimg} alt=""/></a>
              )
            })
          }
          <a href="##"><img src="/assets/img/fx02.png" alt=""/></a>
        </div>

        <article className="product-text">
          <span>{data.title}</span>
          <span className="price">￥{data.xiangj} <s>￥{data.yuanj}</s></span>
        </article>

        <a id="norms" href="##" className="select-btn select-btn-s" onClick={()=>{this.setState({zt:{display:"block"}})}} >选择规格<i className="icon icon-select"></i></a>
        {/* <!-- 选择规格 --> */}
        <div className="norms-box" style={this.state.zt}>
    	    <div className="norms-box-bg"></div>
          <div className="norms-content">
              <div className="norms-content-t norms-content-one">
                  <div className="norms-img"><img src={data.img} alt=""/></div>
                  <div className="norms-c-text">
                      <span>{data.title}</span>
                      <span className="price">￥{data.xiangj} <s>￥{data.yuanj}</s></span>
                  </div>
                  <div className="norms-off-btn" onClick={()=>{this.setState({zt:{display:"none"}})}} ><img src="/assets/img/off_btn.png" alt=""/></div>
              </div>
              <div className="norms-content-t norms-content-two">
                  数量
                  <span className="icon norms-out"></span>
                  <span>1</span>
                  <span className="icon norms-add"></span>
              </div>
              <div className="norms-content-three">
                  颜色<br/>
                  <span className="pick">黑色</span>
                  <span >白色</span>
              </div>
          </div>
        </div>

        <a href="##" className="select-btn select-btn-t"><img src={data.aut} alt=""/>{data.des}<i className="icon icon-select"></i></a>
        <a href="##" className="select-btn select-btn-t select-btn-d"><img src="/assets/img/tel.png" alt=""/>020-8888-8888</a>
        {/* <!-- 详情内容 product-content --> */}
        <div className="product-content">
          <ul id="tab_btn" className="product-content-ul">
            <li className="pick">
              <span>图文详情</span>
            </li>
            <li>
              <span>用户评价</span>
            </li>
          </ul>
          <ul className="product-content-ul2">
            <li className="tab_content show">
              {
                imgs.map((item)=>{
                  return (
                    <img src={item.bimg} alt="" key={item.ids}/>
                  )
                })
              }
              <img src="/assets/img/xq03.jpg" alt=""/>
            </li>
            <li className="tab_content">
              用户评价
              <div className="evaluate">
                <div className="evaluate-head">
                  <img src="/assets/img/user-img0.jpg" alt=""/>
                  <p>郭小帅 <span>2016-08-24</span></p>
                  <p><img src="/assets/img/xingxing.png" alt=""/></p>
                </div>
                <p>这个包包不错，值得拥有</p>
              </div>
              <div className="evaluate">
                <div className="evaluate-head">
                  <img src="/assets/img/user-img0.jpg" alt=""/>
                  <p>郭小帅 <span>2016-08-24</span></p>
                  <p><img src="/assets/img/xingxing.png" alt=""/></p>
                </div>
                <p>这个包包不错，值得拥有</p>
              </div>
              <div className="evaluate">
                <div className="evaluate-head">
                  <img src="/assets/img/user-img0.jpg" alt=""/>
                  <p>郭小帅 <span>2016-08-24</span></p>
                  <p><img src="/assets/img/xingxing.png" alt=""/></p>
                </div>
                <p>这个包包不错，值得拥有</p>
              </div>
            </li>
          </ul>
        </div>

        {/* <!-- 加入购物车底部 --> */}
        <div className="add-shopping">
          <a href="##" className="shopping-btn">
            <em>2</em>
            <i className="icon icon-car"></i>
          </a>
          <div>
            <NavLink to="/order" className="btn btn-red fr">立即购买</NavLink>
            <a href="##" className="btn btn-yellow fr">加入购物车</a>
          </div>
          
          <div className="cl"></div>
        </div>

      </div>
    )
  }
}