import React from 'react';

export default class SiteAdd extends React.Component{
  render(){
    return (
      <div className="SiteAdd">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>新增地址</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div>  

        <article className="no-pad">
          <form>
            <div className="input-row">
                <label>联系人</label>
                <input type="text" placeholder="请填写联系人姓名"/>
              </div>
              <div className="input-row">
                <label>手机号</label>
                <input type="text" placeholder="请填写手机号码"/>
              </div>
              <div className="input-row">
                <label>所在地区</label>
                <input type="text" />
              </div>
              <div className="input-row">
                <label>详细地址</label>
                <input type="text" placeholder="街道"/>
              </div>
              <div className="setCmp-input select-Industry addSite-radio">
              <div className="payment">
                <input type="radio" className="radio-la" value="设置为默认地址" id="check-2" name="doc-radio-1"/>
                      <label htmlFor="check-2">设置为默认地址</label>
              </div>		
            </div>
          </form>
        </article>

        <div className="add-site">
          <a href="##" className="btn bottom-btn">保存地址</a>	
        </div>

      </div>
    )
  }
}