import React from 'react';
import './css/Footer.css';
import {NavLink} from 'react-router-dom';
export default class Footer extends React.Component{
  render(){
    return (
      <div className="Footer">
            <footer>
              <NavLink activeClassName="pick" to="/home">
                <i className="icon icon-f1"></i>
                首页
              </NavLink>
              <NavLink activeClassName="pick" to="/unity">
                <i className="icon icon-f2"></i>
                社区
              </NavLink>
              <NavLink activeClassName="pick" to="/car">
                <i className="icon icon-f3"></i>
                购物车
              </NavLink>
              <NavLink activeClassName="pick" to="/user">
                <i className="icon icon-f4"></i>
                我
              </NavLink>
          </footer>
      </div>
    )
  }
}