import React from 'react';
import {NavLink} from 'react-router-dom';
export default class Car extends React.Component{
  render(){
    return (
      <div className="Car">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>购物车</h1>
              <a href="##" className="cart-edit">编辑</a>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div>

        <form className="setCmp">
          <div className="Cart">
            <div className="payment">
              <input type="radio" className="radio-la" value="" id="check-2" name="doc-radio-1"/>
                    <label htmlFor="check-2"></label>
            </div>
            <a href="##" className="select-btn select-btn-t"><img src="/assets/img/sjlogo.png" alt=""/>海绵包包</a>
          </div>
          <article className="confirmOrder">
            <img src="/assets/img/cp_gg.png" alt=""/>
            <div className="product-text">
                <span>首款海绵包包</span>
                <span className="hue">颜色：黑色</span>
                <span className="price price-cart">
                  ￥68.00
                  <div className="norms-content-t norms-content-two">
                          <span className="icon norms-out"></span>
                          <span>1</span>
                          <span className="icon norms-add"></span>
                      </div>
                </span>
            </div>
          </article>
          <div className="Cart">
            <div className="payment">
                <input type="radio" className="radio-la" value="" id="check-3" name="doc-radio-1"/>
                      <label htmlFor="check-3"></label>
              </div>	
            <a href="##" className="select-btn select-btn-t"><img src="/assets/img/sjlogo.png" alt=""/>海绵包包</a>
          </div>
          <article className="confirmOrder">
            <img src="/assets/img/cp_gg.png" alt=""/>
            <div className="product-text">
                <span>首款海绵包包</span>
                <span className="hue">颜色：黑色</span>
                <span className="price price-cart">
                  ￥68.00
                  <div className="norms-content-t norms-content-two">
                          <span className="icon norms-out"></span>
                          <span>1</span>
                          <span className="icon norms-add"></span>
                      </div>
                </span>
            </div>
          </article>
        </form>

        <nav className="navbar-fixed-bottom navbar-fixed-bottom-cart">
          <div className="container container-cart">
              <div className="navbar-text navbar-left pull-left m-cart-disbursement">		合计：￥136.00
              </div> 
              <NavLink className="btn btn-warning navbar-btn pull-right" to="/order">去结算</NavLink>
            </div>	
        </nav>

      </div>
    )
  }
}