import React from 'react';
import { Link } from 'react-router-dom'
export default class Reg extends React.Component{

  state = {
    user:'',
    content:'',
    content1:'',
    list:[]
  };

  changeUser=(ev)=>{
    this.setState({user:ev.target.value})
  };

  changeContent=(ev)=>{
    this.setState({content:ev.target.value})
  };

  changeContent1=(ev)=>{
    this.setState({content1:ev.target.value})
  };

  render(){
    return (
      <div className="Reg">
        <div>
          <header>
            <div className="header">
              <a href="##" onClick={()=>this.props.history.go(-1)}><i className="icon icon-return"></i></a>
              <h1>注册</h1>
            </div>
          </header>
          <div style={{height: "2.5rem"}}></div>
        </div> 

        <div className="Login-text Login-t Login-t-t">
          <label>账号:</label>
          <input type="text" className="Login-user" placeholder="请输入手机号/邮箱" value={this.state.user} onChange={this.changeUser} />
        </div>
        
        <div className="Login-pass Login-t Login-t-t">
          <label>密码:</label>
          <input type="password" className="Login-content" placeholder="请输入密码" value={this.state.content} onChange={this.changeContent} />
        </div>

        <div className="Login-pass Login-t Login-t-t">
          <label>确认:</label>
          <input type="password" className="Login-content1" placeholder="确认密码" value={this.state.content1} onChange={this.changeContent1} />
        </div>

        <div className="Login-btn Login-t">
            <button>注册</button>
        </div>
        
        <div className="Login-zhuche Login-t"><Link to="/login">前往登录</Link></div>

      </div>
    )
  }
}